.select2-container {
	margin: 0 0 1rem 0;
	position: relative;
	vertical-align: middle;
	font-family: inherit;
	-webkit-appearance: none !important;
	font-size: 0.875rem;

	background: #fafafa url("data:image/svg+xml;base64,") no-repeat;
	background-position-x: 100%;
	background-position-y: center;

}
.select2-container:hover {
	background-color: #f2f2f2;
	border-color: #999;
}

.error .select2-container {
	margin-bottom: 0;
}

.select2-container,
.select2-drop,
.select2-search,
.select2-search input {
  /*
	Force border-box so that % widths fit the parent
	container without overlap because of margin/padding.

	More Info : http://www.quirksmode.org/css/box.html
  */
  -webkit-box-sizing: border-box; /* webkit */
	 -moz-box-sizing: border-box; /* firefox */
		  box-sizing: border-box; /* css3 */
}

.select2-container .select2-choice {
	display: block;
	overflow: hidden;
	position: relative;
	height: 2.315rem;
	border: 1px solid #ccc;
	padding: 0.5rem 1rem 0.5rem 0.5rem;

	white-space: nowrap;
	line-height: 1.3125rem;
	color: inherit;
	text-decoration: none;

	-webkit-box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
	   -moz-box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
	        box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);

	-webkit-touch-callout: none;
	  -webkit-user-select: none;
		 -moz-user-select: none;
		  -ms-user-select: none;
			  user-select: none;

}

.select2-container.select2-drop-above .select2-choice {
	/*border-bottom-color: #aaa;*/
}

.select2-container.select2-allowclear .select2-choice .select2-chosen {
	margin-right: 42px;
}

.select2-container .select2-choice > .select2-chosen {
	margin-right: 1rem;
	display: block;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}

.select2-container .select2-choice abbr {
	display: none;
	width: 9px;
	height: 9px;
	position: absolute;
	right: 24px;
	top: 13px;

	font-size: 1px;
	text-decoration: none;

	border: 0;
	background: transparent url("data:image/svg+xml;base64,") 50% 50% no-repeat;
	cursor: pointer;
	outline: 0;
	opacity: 0.5;
}

.select2-container.select2-allowclear .select2-choice abbr {
	display: inline-block;
}

.select2-container .select2-choice abbr:hover {
	/*background-position: right -11px;*/
	opacity: 1;
	cursor: pointer;
}

.select2-drop-mask {
	border: 0;
	margin: 0;
	padding: 0;
	position: fixed;
	left: 0;
	top: 0;
	min-height: 100%;
	min-width: 100%;
	height: auto;
	width: auto;
	opacity: 0;
	z-index: 9998;
	/* styles required for IE to work */
	background-color: #fff;
	filter: alpha(opacity=0);
}

.select2-drop {
	width: 100%;
	margin-top: -1px;
	position: absolute;
	z-index: 9999;
	top: 100%;

	background: #fafafa;
	color: #000;
	border: 1px solid #999;
	border-top: 0;

	outline: none;

	-webkit-box-shadow: 0 3px 5px -2px #999999;
	   -moz-box-shadow: 0 3px 5px -2px #999999;
	        box-shadow: 0 3px 5px -2px #999999;

/*
	padding-top: 2.315rem;
	margin-top: -2.315rem;
*/
}


.select2-drop-auto-width {
	border-top: 1px solid #aaa;
	width: auto;
}

.select2-drop-auto-width .select2-search {
	padding-top: 4px;
}

.select2-drop.select2-drop-above {
	/*margin-top: 1px;*/
	/*border-top: 1px solid #aaa;*/
	/*border-bottom: 0;*/

	padding-top: 0;
	margin-top: 0;
	margin-bottom: -4.630rem;
	padding-bottom: 2.315rem;

}

.select2-drop-active {
	border: 1px solid #999;
	border-top: none;
}

.select2-drop.select2-drop-above.select2-drop-active {
	border-top: 1px solid #999;
}

.select2-container .select2-choice .select2-arrow {
	display: none;
/*    width: 18px;
	height: 100%;
	position: absolute;
	right: 0;
	top: 0;

	border-left: 1px solid #aaa;
	border-radius: 0 4px 4px 0;

	background-clip: padding-box;

	background: #ccc;
